"use client"

import { useEffect, useState } from 'react';
import { api } from "../../../convex/_generated/api";
import { useQuery } from 'convex/react';
import { FaRegCopy } from 'react-icons/fa';
import {FaHandPointRight} from "react-icons/fa"


const Saved = () => {
  const [bg, setBG] = useState("white");
  const [message, setMessage] = useState("Was Created and Saved:");

 function copyNote(note) {
    navigator.clipboard.writeText(note);  
    setMessage("Copied to clipboard!");
    setTimeout(() => setMessage("Was Created and Saved by You "), 1500);
  }

  useEffect(() => {
    const savedColor = localStorage.getItem("cl");
    if (savedColor) setBG(savedColor);

  
    
  }, []);

  const notes = useQuery(api.notes.getNotes, {});
  
  return (
    <div style={{ backgroundColor: bg }} className='flex flex-col h-fit min-h-screen justify-start items-center text-black/90 pt-25'>
      <h2 className='text-2xl font-bold mb-4 py-20 opacity-30  px-5 scale-50 border-8 border-blue-200/60 bg-black/40 shadow-green-50/60 shadow-xl text-blue-300/80 flex gap-4 top-70 opacity-50 px-2 rounded-2xl m-2 ring-blue-100 ring-2 animate-fade text-white fixed left-10'>Your Saved Notes<div className="text-white"><FaHandPointRight/></div></h2>
      {notes && notes.length > 0 ? (
        <ul className='w-full max-w-4xl grid grid-cols-7 gap-10 border-white'>
          {notes.map((note,idx) => (
           <div className=' p-9 m-10 hover:z-1000 cursor-grabbing duration-1500  animate-r1 hover:animate-none hover:scale-110 '>
             <li key={idx} className='size-100 border-8 border-black/60 opacity-80 hover:opacity-100 bg-white/95 shadow-green-50/30 shadow-xl  rounded-xl  p-4 mt-5 mb-4  ring-blue-200/60 w-70 h-100 ring-4 border-blue-100'>
              <div className=' text-lg flex justify-between opacity-75 font-extrabold hover:opacity-80 font-sans'>{note.title}
                <button onClick={()=>copyNote(note.content)}  className="rounded-2xl  hover:bg-blue-200/70 cursor-pointer hover:text-blue-800/60 border h-fit w-fit p-1 text-blue-400/70"><FaRegCopy></FaRegCopy></button></div>
              <p className='text-gray-700 font-serif text-[0.9rem]'>{note.content} </p>
              <span className='text-xs text-gray-400'>{message}</span>
            </li>
           </div>
          ))}
        </ul>
      ) : (
        <p className='mt-30 text-blue-200/60'>No Saved Notes</p>
      )}
    </div>
  );
}

export default Saved;